export const debugRecipeMapping = () => {
  console.log(' ===     ===');
  
  //   ID
  const testRecipeIds = [
    'mankae-7014691', // 
    'mankae-7014692', // 
    'mankae-7014693', // 
    'mankae-7014694', // 
    'mankae-7014695', // 
    'mankae-7014698', // 
    'diet-protein-breakfast', //  
    'muscle-power-lunch', //  
    'test-unknown-recipe' //    
  ];
  
  testRecipeIds.forEach(recipeId => {
    console.log(`\n   ID: ${recipeId}`);
    try {
      //  import  
      import('../data/recipeIngredients').then(({ getRecipeIngredientsData }) => {
        const ingredients = getRecipeIngredientsData(recipeId);
        console.log(`   : ${ingredients.length}`);
        console.log(` :`, ingredients.map(ing => `${ing.product.name} (${ing.quantity}${ing.unit})`));
      });
    } catch (error) {
      console.error(` :`, error);
    }
  });
};

//      window  
if (typeof window !== 'undefined') {
  (window as any).debugRecipeMapping = debugRecipeMapping;
}

export default debugRecipeMapping;
